* {
    box-sizing: border-box;
    
}
body {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
}

main,nav {
    border: 1px solid black;
    border-radius: 25px;
}
header {
    grid-column: 1/-1;
    grid-row: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
header span {
    font-weight: bold;
    font-size: 1.5rem;
}
header a {
    margin: 5px 5%;
}
nav {
    grid-row: 2/-1;
    grid-column: 1/3;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 15px;
}
main {
    grid-column: 3/-1;
    grid-row: 2/-1;
}
nav button {
    border-radius: 20%;
    background-color: rgb(224, 222, 222);
    width: 75%;
    margin: 15px 15px;
    
}